@import "mixins";
$mainColor: rgb(231, 174, 95);

.navbar {
    width: 100%;
    min-height: 8vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(41, 40, 40);
    box-shadow: rgb(232, 187, 133, 0.5) 2px 2px 2px;
    
    &__items {
        width: 80%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .burgerMenu {
            display: none;
        }

        .logo {
            text-transform: uppercase;
            text-decoration: none;
            font-size: 2rem;
            color: rgb(231, 174, 95);
            font-weight: bold;
        }

        &-list {
            display: flex;
            justify-content: space-between;
            list-style: none;
            width: 25%;

            &-link {
                font-family: 'Montserrat', sans-serif;
                font-size: 1rem;
                font-weight: bold;
                transition: all ease-in-out 0.2s;
                text-decoration: none;
                color: rgb(231, 174, 95);
            }

            &-link:hover {
                color: rgb(174, 174, 174);
            }
        }
    }
}

@include phone {
    .hidden {
        display: none;
    }

    .navbar {
        min-width: 100vw;
        
        .burgerMenu {
            display: block;
        }
        
        .burger__menu {
            list-style: none;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            align-items: center;
            position: absolute;
            right: 0;
            top: 10%;
            background-color: rgb(156, 156, 156);
            height: 15%;
            margin-right: 1rem;
            width: 25%;
            padding: 10px 0 10px 0;
            border-radius: 3px;
            transition: all ease-in-out 0.5s;
            box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;

            &-links {
                text-decoration: none;
                color: $mainColor;
            }
        }
    }
}